export default function ContactSection() {
  return (
    <section className="py-16 px-4 bg-[#23234a] text-cream" data-aos="fade-in">
      <h2 className="text-3xl md:text-4xl font-serif font-bold text-gold mb-10 text-center" data-aos="zoom-in">Contact Us</h2>
      <div className="max-w-3xl mx-auto flex flex-col md:flex-row gap-8 items-center justify-center" data-aos="fade-up">
        <div className="flex-1 text-center md:text-left">
          <p className="mb-2 text-lg">Phone: <a href="tel:+91XXXXXXXXXX" className="text-gold font-semibold">+91 [XXXXXXXXXX]</a></p>
          <p className="mb-2 text-lg">Email: <a href="mailto:contact@yourhall.com" className="text-gold font-semibold">contact@yourhall.com</a></p>
          <p className="mb-2 text-lg">Location: Nurpur, Himachal Pradesh, India</p>
          <p className="mb-2 text-lg">Operating Hours: 9am - 9pm</p>
          <div className="flex gap-4 mt-4 justify-center md:justify-start">
            <a href="#" className="text-gold hover:text-cream text-2xl" aria-label="Instagram">&#x1F465;</a>
            <a href="#" className="text-gold hover:text-cream text-2xl" aria-label="WhatsApp">&#x1F4F1;</a>
            <a href="#" className="text-gold hover:text-cream text-2xl" aria-label="Facebook">&#x1F426;</a>
          </div>
        </div>
        <div className="flex-1">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3424.0000000000005!2d76.00000000000001!3d32.00000000000001!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0:0x0!2sAashirvad+Banquet+Hall!5e0!3m2!1sen!2sin!4v0000000000000!5m2!1sen!2sin"
            width="100%"
            height="200"
            style={{ border: 0 }}
            allowFullScreen=""
            loading="lazy"
            referrerPolicy="no-referrer-when-downgrade"
            title="Aashirvad Banquet Hall Location"
          ></iframe>
        </div>
      </div>
      <form className="mt-8 max-w-xl mx-auto bg-[#1a1a2e] p-8 rounded-xl shadow-lg" data-aos="fade-up" data-aos-delay="200">
        <h3 className="text-xl font-semibold text-gold mb-4">Contact Form</h3>
        <input type="text" placeholder="Your Name" className="w-full mb-4 p-3 rounded bg-[#23234a] text-cream" disabled />
        <input type="email" placeholder="Your Email" className="w-full mb-4 p-3 rounded bg-[#23234a] text-cream" disabled />
        <textarea placeholder="Your Message" className="w-full mb-4 p-3 rounded bg-[#23234a] text-cream" rows={4} disabled></textarea>
        <button type="submit" className="bg-gold text-[#1a1a2e] px-6 py-2 rounded-full font-semibold" disabled>Send (Static)</button>
      </form>
    </section>
  );
}
